header {
  text-align: center;
}

header h1 {
  text-align: center;
  color: #BBB;
  margin: 0;
  font-size: 4rem;
  text-align: center;
}

header p {
  text-align: center;
  color: #BBB;
  font-weight: 700;
  margin-bottom: 2.75em;
}

header a {
  color: #BBB;
  font-weight: 400;
  padding: 1.25rem 2.5rem;
  -webkit-box-shadow: 0 .15em 0 .20em rgba(0, 0, 0, 0.1);
          box-shadow: 0 .15em 0 .20em rgba(0, 0, 0, 0.1);
  background-color: #1d809f;
  border-color: #1d809f;
  text-decoration: none;
}



