.services{
  background-color: #6E9A98;
  paddind-top: 30em;
  paddind-bottom: 30em;
  text-align: center;
  height: 31em;
}

.services h3 {
  font-size: 1rem;
  text-transform: uppercase;
  color: rgb(236, 184, 7);
  margin: 0;
  padding: 0;
}

.services h2 {
  font-size: 4rem;
  color: #BBB;
  margin-bottom: .5em;
  margin-top: 0em;
}

.services > .row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.services > .row
div > .large {
  float: left;
  font-size: 36px;
  background-color: #BBB;
  width: 7em;
  height: 6.5em;
  padding-top: 0.5em;
  border-radius: 50%;
  color: #6E9A98;
  line-height: 1em;
}

.services > .row
div > .medium {
  float: left;
  font-size: 24px;
  background-color: #BBB;
  width: 4em;
  height: 3.5em;
  padding-top: 0.5em;
  border-radius: 50%;
  color: #6E9A98;
  line-height: 1em;
}

.services > .row div p {
  color: #BBB;
}

h3 {
  margin-top: 3em;
}



